import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { UnitComponent } from "app/system-setup/pms-setup/components/unit/unit.component";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";

const routes: Routes = [
  {
    path: "unit",
    component: UnitComponent,
    data: {view: ViewsManager.unit},
    canActivate: [CheckIsViewAllowedGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PMSSetupRoutingModule {}
