import {Injectable} from '@angular/core';
import {AppClientService} from 'app/shared/services/app-client.service';
import {IUnitModel, IUnitRequest} from 'app/system-setup/pms-setup/models/unit';
import {IResponse, IResponseList} from 'app/shared/interfaces/responces';
import {IPageParams} from 'app/shared/interfaces/params';

@Injectable({
  providedIn: "root",
})
export class UnitService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IUnitRequest) {
    return this.post<IResponse<IUnitModel>>("UnitSetup/Create", data);
  }

  update(data: IUnitRequest) {
    return this.post<IResponse<IUnitModel>>("UnitSetup/Edit", data);
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IUnitModel[]>>("UnitSetup/GetList", {
      params: params,
    });
  }

  getAll() {
    return this.get<IResponse<IUnitModel[]>>("UnitSetup/GetAll")
    // .pipe(
    //   map((res: IResponse<IUnitModel[]>) => {
    //     return res.data;
    //   })
    // );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("UnitSetup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
